.input1{


background: #FFFFFF;
border: 1px solid #0F1F78;
border-radius: 8px;
height: 48px;
margin:0px 24px 0px 24px;
box-sizing: border-box;
}

.box{

box-sizing: border-box;
padding-right: 4px;
margin:0px 24px 0px 24px;
height: 28px;

border: 1px solid rgba(0, 0, 0, 0.2);
border-radius: 8px;
}

.boxtxt{
    
font-family: 'Poppins';
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 20px;
padding-left: 24px;
color: #6D747A;
flex: none;
order: 0;
flex-grow: 0;

}

.input2{

box-sizing: border-box;

height: 48px;
left: 0%;

margin:0px 24px 0px 24px;
/* Blacks/White */
font-family: 'Poppins';
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 20px;
/* identical to box height, or 143% */


/* Blacks/Darker grey */

color: #6D747A;
padding-left: 16px;
background: #FFFFFF;
/* lighter lighter grey */

border: 1px solid #CED4DA;
border-radius: 8px;
}

.input3{

box-sizing: border-box;

height: 48px;

margin:0px 24px 0px 24px;

font-family: 'Poppins';
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 20px;
/* identical to box height, or 143% */


/* Blacks/Darker grey */

color: #6D747A;

/* Blacks/Light grey */

background: #EBEDF0;
/* lighter lighter grey */

border: 1px solid #CED4DA;
border-radius: 8px;
padding-left: 14px;
}

.button{
    padding: 16px 24px;
    margin: 32px 24px auto 24px;
border: none;

font-family: 'Poppins';
font-style: normal;
font-weight: 600;
font-size: 16px;
line-height: 18px;
/* identical to box height, or 112% */

text-align: center;

/* Blacks/White */

color: #FFFFFF;
height: 50px;

/* Primary/100 */

background: #283891;
border-radius: 8px;
}